import { Component, OnInit, Input } from '@angular/core';
import { Food } from '../food.model';
import { OrderServiceService } from '../service/order-service.service';
import { OrderItems } from '../order.model';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.scss']
})
export class OrderComponent implements OnInit {
  size: string;
  quantity: number;
  total:number = null;
  @Input('foodItem') foodItem:Food;
  constructor(private orderService: OrderServiceService) { }

  ngOnInit(): void {
    this.size=""
    this.quantity=0
  }
  less(){
    if (this.quantity>0){
      this.quantity--;
    }
    this.calculatePrice()
  }
  more(){
    this.quantity++;
    this.calculatePrice()
  }
  onSizeChange(size){
    this.size=size.srcElement.value;
    this.calculatePrice()
  }
  calculatePrice(){
    this.total = this.quantity * this.foodItem[this.size];
  }
  addOrder(){
    let order = new OrderItems();
    order.size = this.size
    order.foodname = this.foodItem.foodname
    order.quantity = this.quantity
    order.price = this.total

    this.orderService.onAddOrder(order);
    console.log(this.orderService.onGetOrder())
  }
}
